.login {
  width: 100%;
  height: 100vh;
  background-color: black;
  background: url("../../assets/images/starting.png");
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.content {
  display: grid;
  grid-template-columns: 1fr 0.85fr;
  gap: 30px;
  @media (max-width: 786px) {
    grid-template-columns: 1fr;
    margin-top: -20%;
  }
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  & h2 {
    font-weight: bold;
    font-size: 136px;
    color: white;
    @media (max-width: 1006px) {
      font-size: 90px;
    }
    @media (max-width: 786px) {
      font-size: 70px;
    }
  }
  & h1 {
    font-weight: bold;
    font-size: 195px;
    color: white;
    @media (max-width: 1006px) {
      font-size: 130px;
    }
    @media (max-width: 786px) {
      font-size: 100px;
    }
  }
  & p {
    color: white;
  }
}

.form {
  background-color: #fff;
  box-shadow: 0px 4px 57px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
  padding: 52px 41px;
  display: grid;
  grid-template-columns: 1fr;
  height: 130%;
  transform: translateY(-15%);
  max-width: 495px;
  @media (max-width: 1006px) {
    padding: 32px 21px;
  }
  @media (max-width: 786px) {
    transform: translateY(0);
  }

  & h3 {
    font-weight: 600;
    font-size: 32px;
    @media (max-width: 786px) {
      text-align: center;
    }
  }

  & label {
    display: flex;
    flex-direction: column;
    font-size: 22px;
  }
  & input {
    padding: 17px 19px;
    border: 1px solid #072253;
    border-radius: 10px;
    margin-top: 5px;
    font-size: 22px;
  }
  & a {
    font-size: 14px;
    justify-self: center;
    & p {
      color: #238ff6;
    }
  }
  &__wrapper {
    width: 100%;
    display: flex;
    max-width: 343px;
    gap: 20px;
    & label,
    & input {
      width: 100%;
    }
  }
}

.btn {
  background: linear-gradient(268.25deg, #7d41e1 6.27%, #238ff6 94.13%);
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  border: none;
  color: white;
  font-size: 24px;
}
