.create-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  margin-bottom: 60px;

  & h1 {
    font-size: 24px;
    margin-bottom: 25px;
    font-weight: 600;
  }
  & img {
    margin-bottom: 24px;
    cursor: pointer;
    width: 98px;
  }
  & p {
    font-size: 20px;
  }
}
