.popup {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 1s;
}

.popup.active {
  opacity: 1;
  pointer-events: all;
}

.content {
  max-width: 580px;
  width: 100%;
  padding: 28px 36px 37px 39px;
  transform: scale(0.5);
  transition: 0.3s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 100;
  background-color: white;
  border-radius: 20px;
}

.content.active {
  transform: scale(1);
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  & h2 {
    font-size: 24px;
    font-weight: 600;
  }
  & img {
    cursor: pointer;
  }
}

.paragraph {
  align-self: flex-start;
  margin-bottom: 14px;
  font-size: 20px;
}

.link {
  align-self: flex-start;
  border-bottom: 2px solid #7d41e1;
  width: 100%;
  font-size: 20px;
  color: #969ead;
  padding: 5px 0;
  margin-bottom: 16px;
  overflow: hidden;
}

.btns {
  display: flex;
  justify-content: flex-end;
  gap: 17px;
  width: 100%;
  & button {
    max-width: 173px;
    width: 100%;
    height: 61px;
    border-radius: 10px;
    font-size: 20px;

    &:nth-child(1) {
      background-color: transparent;
      border: 3px solid #7d41e1;
      color: #7d41e1;
    }
    &:nth-child(2) {
      background: linear-gradient(268.25deg, #7d41e1 6.27%, #238ff6 94.13%);
      color: white;
      border: none;
    }
  }
}
