.header {
  width: 100%;
  height: 86px;
  background: linear-gradient(268.25deg, #7d41e1 6.27%, #238ff6 94.13%);
  display: flex;
  //   align-items: center;
  margin-bottom: 77px;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
}

.logo {
  width: 84px;
  height: 38px;
  & img {
    width: 100%;
    height: 100%;
  }
}

.profile {
  width: 38px;
  height: 38px;
  & img {
    width: 100%;
    height: 100%;
  }
}
