.recent {
  margin-bottom: 100px;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 38px;
}

.list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  @media (max-width: 1205px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.item {
  border: 3px solid #7d41e1;
  border-radius: 10px;
  cursor: pointer;
  width: 180px;
  height: 180px;
  & img {
    margin-bottom: 20px;
    pointer-events: none;
    width: 100%;
  }
  & p {
    font-size: 20px;
    margin-bottom: 20px;
    text-align: center;
  }
}
