.container {
  margin: 0 auto;
  max-width: 780px;
  width: 100%;
}

.form {
  //   width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 100px;
  & label,
  & input {
    width: 100%;
  }
  & label {
    margin-bottom: 11px;
  }
  & label:not(.label_answer) {
    display: block;
  }
  & input {
    border: 3px solid #7d41e1;
    border-radius: 10px;
    font-size: 20px;
    padding: 19px 33px;
    background-color: transparent !important;
  }
}

.label_nameForm {
  border: 3px solid #7d41e1;
  border-radius: 10px;
  padding: 30px 32px;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 39px !important;

  & input {
    margin-top: 15px;
    border: none;
    font-size: 20px;
    font-weight: 500;
    outline: none;
    padding: 5px 0;
  }
}

.btn {
  font-size: 20px;
  color: white;
  background: linear-gradient(268.25deg, #7d41e1 6.27%, #238ff6 94.13%);
  border-radius: 10px;
  border: none;
  max-width: 380px;
  width: 100%;
  height: 61px;
  margin-bottom: 6px;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 100%;
  & label {
    display: block;
    border: 3px solid #7d41e1;
    border-radius: 10px;
    text-align: center;
    padding: 15px 0;
    font-size: 20px;
    cursor: pointer;
    transition: 0.3s;
    &.active {
      background-color: #7d41e1;
      color: white;
    }
    & input {
      appearance: none;
      width: 0;
      height: 0;
      padding: 0;
      border: none;
    }
  }
}

.label_answer {
  display: flex;
  align-items: center;
  position: relative;
  & input {
    border: none;
    outline: none;
    padding: 7px 20px;
    display: inline-block;
  }
}

.typeInput {
  width: 37px;
  height: 37px;
  border: 3px solid #7d41e1;
  transition: 0.3s;
  &.active {
    border-radius: 50%;
  }
}

.plus {
  width: 37px;
  height: 37px;
  align-self: flex-start;
  margin-bottom: 63px;
  cursor: pointer;
  & img {
    width: 100%;
    height: 100%;
  }
}

.question {
  margin-bottom: 60px;
  width: 100%;
}

.margin-bottom {
  margin-bottom: 63px;
}

.deleteBtn_answer {
  height: 100%;
  position: absolute;
  right: -50px;
  top: 0;
  & img {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
}

.label_question {
  position: relative;
}

.deleteBtn_question {
  height: 100%;
  position: absolute;
  right: -80px;
  top: 0;
  & img {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
}
